import React, {useState} from 'react';
import PropTypes from 'prop-types'
import {compose} from "redux";
import {paymentDataRequest, setDialog} from "../Redux/profile/profile-reducer";
import {connect} from "react-redux";
import {getLogOut} from "../Redux/auth/auth-reducer";
import MyButton from "../Button/MyButton";
import styles from './Profile.module.css'
import {
    getCardNumber,
    getCvc,
    getExpiryDate,
    getIsLoggedIn,
    getName, getShowDialog,
    getToken
} from "../Redux/profile/profile-selector";
import form from "../LoginForm/LoginForm.module.css";
import {withStyles} from "@material-ui/styles";
import logo from '../../assets/img/card-logo.png'
import secImg from '../../assets/img/sec.png'
import {getPaidStatus} from "../Redux/map/map-selector";
import SimpleDialog from "../SimpleDialog/SimpleDialog";
import {Field, Form} from "react-final-form";
import {DatePicker, TextField} from 'mui-rff';
import dayjs from "dayjs";
import formatString from "format-string-by-pattern";



const stylesMaterial = {
    input: {
        marginBottom: '20px'
    },
    inputRow: {
        width: '47%',
        marginRight: '20px',
        '&:last-child': {
            marginRight: '0'
        }
    },
    cardRow: {
        height: '20%',
        display: 'flex',
        justifyContent: 'space-between',
        alignItems: 'center'
    }
}

const Profile = (props) => {


    const [selectedDate, setSelectedDate] = useState(new Date());

    const onSubmit = (values) => {

        props.paymentDataRequest(values.name, dayjs(values.expiryDate).format("MM/YY"), values.cardNumber, values.cvc)
    }


    const validate = values => {
        const errors = {};
        if (!values.name) {
            errors.name = 'Required';
        }
        if (!values.cardNumber || isNaN(values.cardNumber)) {
            errors.cardNumber = 'Required and should be a number';
        }
        if (!values.cvc) {
            errors.cvc = 'Required';
        }
        return errors;
    };
    const masks = [
        { name: "phone-1", parse: "999-999-9999" },
        { name: "phone-2", parse: "(999) 999-9999" }
    ];


    if (props.showDialog) {
        return <SimpleDialog
            title={'Профиль'}
            subtitle={'Платёжные данные обновлены. Теперь вы можете заказывать такси'}
            setDialog={props.setDialog}
            link={'/map'}
            linkValue={'Перейти на карту'}
            open={true}/>
    }
    return (

        <div >

            <Form
                onSubmit={onSubmit}
                initialValues={{name: '', cardNumber: '', expiryDate: '', cvc: ''}}
                validate={validate}
                render={({handleSubmit, form, submitting, pristine, values}) => (
                    <div className={styles.wrapper}>
                    <form onSubmit={handleSubmit} noValidate className={styles.col}>
                        <TextField
                            className={props.classes.input}
                            label="Имя"
                            name="name"
                            margin="none"
                            required={true}
                        />
                        <TextField
                            className={props.classes.input}
                            label="Номер карты"
                            name="cardNumber"
                            margin="none"
                            required={true}
                            parse={formatString('111-111')}
                        />
                        {/*<Field*/}
                        {/*    component="input"*/}
                        {/*    name='cardNumber'*/}
                        {/*    parse={formatString('0000 0000 0000 0000')}*/}
                        {/*    placeholder={'Номер карты'}*/}
                        {/*/>*/}

                        <div className={styles.formRow}>

                            <DatePicker
                                name="expiryDate"
                                variant="dialog"
                                format="MM/YY"
                                openTo="year"
                                views={["year", "month"]}
                                label="Year and Month"

                            />

                            <TextField
                                className={props.classes.inputRow}
                                label="cvc"
                                name="cvc"
                                margin="none"
                                type='password'
                                required={true}
                            />
                        </div>

                        <MyButton type="submit" value={'Сохранить'}/>
                    </form>
                        <div className={styles.col}>
                            <div className={styles.card}>
                                <div className={props.classes.cardRow}>
                                    <div className={styles.imgWrapper}><img src={logo} alt=""/></div>
                                    <input className={styles.cardDate} type="text" placeholder={'mm/yy'} value={values.expiryDate && dayjs(values.expiryDate).format("MM/YY")} disabled
                                    />
                                </div>

                                <input className={styles.cardNumber} type="text" placeholder={'0000 0000 0000 0000'} value={values.cardNumber} disabled
                                />
                                <div className={styles.cardIcons}>
                                    <img src={secImg} alt=""/>
                                </div>
                            </div>
                        </div>
                    </div>
                )}
            />

        </div>
    );
};

let mapStateToProps = (state) => {
    return {
        name: getName(state),
        month: getExpiryDate(state),
        card: getCardNumber(state),
        cvc: getCvc(state),
        isLoggedIn: getIsLoggedIn(state),
        token: getToken(state),
        isPaid: getPaidStatus(state),
        showDialog: getShowDialog(state)
    }
}


Profile.propTypes = {
    text: PropTypes.string,
}
let Compose = compose(
    connect(mapStateToProps, {paymentDataRequest, getLogOut, setDialog}),
    withStyles(stylesMaterial)
)(Profile)

export default Compose
